.mobile(@rules) {
    @media only screen and (max-width: 500px) {
        @rules();
    }
}

.tablet(@rules) {
    @media only screen and (min-width: 500px) and (max-width: 750px) {
        @rules();
    }
}

.desktop(@rules) {
    @media only screen and (min-width: 750px) {
        @rules();
    }
}



